::selection {
    color: #fff;
    background-color: $secondary-color;
}

body {
    background-color: $bg-primary;
    color: $text-color;
    font-family: $font-primary;
}

section {
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: $section-margin-lg;
    max-width: 1920px;

    &:last-of-type {
        margin-bottom: 56px;
    }

    &.container-fluid {

        padding: 90px 85px 100px;
        position: relative;

        &:before {
            content: "";
            position: absolute;
            left: 42px;
            right: 42px;
            top: 0;
            height: 100%;
            background-color: $bg-secondary;
            border-radius: 20px;
            z-index: -1;
        }
    }

    &:nth-child(odd) {
        .template-title {
            &:after {
                background-color: $primary-color;
            }
        }
    }
}

.container {
    max-width: 1170px;
    padding-right: 20px;
    padding-left: 20px;
    margin-right: auto;
    margin-left: auto;
}

.container-xxl {
    max-width: 1820px;
}

.btn {
    font-weight: 600;
    font-size: 15px;
    line-height: 146%;
    border-radius: 90px;
    padding: 13px 17px;
    display: inline-flex;
    align-items: center;
    letter-spacing: 0.6px;

    &:hover {
        .icon {
            filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(49deg) brightness(180%) contrast(101%);
        }
    }

    &:focus-visible {
        box-shadow: none;
    }
    &:focus {
        box-shadow: none;
    }
}

.btn-primary {
    background-color: $primary-color;
    border-color: $primary-color;

    &:hover,
    &:active,
    &:focus {
        background-color: $secondary-color;
        border-color: $secondary-color;
    }
    
}

.btn-secondary {
    background-color: transparent;
    border-color: $primary-color;

    &:hover,
    &:active,
    &:focus {
        background-color: $secondary-color;
        border-color: $secondary-color;
    }
}

.btn-lg {
    padding: 19.6px 34px;
    font-size: 16px;

    .icon {
        margin: 0 -14px 0 47px;
        width: 27px;
        height: 27px;
        transition: .3s;
    }
}

